/* eslint-disable no-console */
import React from "react"
import { connect } from "react-redux"
import PropTypes from "prop-types"

import Editor from "components/Editor/Editor.js"

import META_17820 from "utils/const/scheme/meta17820.js"
import MESSAGE from "utils/const/message.js"

import {setTrack} from "store/actions/sound.js"

class SchemeMeta17820 extends React.Component {
    constructor(props){
        super(props);

        this.lastTimerID = null;

        this.state = {
            scheme: JSON.parse(JSON.stringify(META_17820)), // deep copy obj
        }
    }

    componentDidUpdate(prevProps) {
        // при появлении новых настроек сверху пересматр схемы с новыми настройками
        if (prevProps.settings !== this.props.settings) {
            const scheme = {...this.state.scheme};
            this.getCheckScheme(scheme, this.props.settings)
        }
    }
    componentWillUnmount(){
        clearTimeout(this.lastTimerID);

        // остановить трек звук
        this.props.setTrack(``, false);
    }

    setSounds2ForFire() {
        const scheme = {...this.state.scheme};
        scheme.settings.setSounds2ForFire = true;
        this.getCheckScheme(scheme);
    }
    getActiveZone(lineToSpeakerErr, activeZone = [false, false, false, false, false, false, false, false]) {
        const result = [];

        // если ошибка в линии, то линия не активна
        for (let i = 0; i < 8; i++) {
            result[i] = lineToSpeakerErr[i] ? false : activeZone[i]
        }

        return result
    }

    getCheckScheme(scheme, settings) {
        const {elements, message} = scheme;
        let track = ``; // нет звука,
        let loopNewSound = true; // нет звука,

        // пришли новые настроки схемы от родителя
        if (settings) {
            scheme.settings = settings
        }

        // pult - ссылка на пульта
        const meta18580 = elements[23];

        const meta17820 = {
            // работающие динамики и плюсики на экране
            activeZone: [false, false, false, false, false, false, false, false],

            // зоны в которых пожар
            fireZone: [false, false, false, false, false, false, false, false],

            // зоны выбранные на пульте
            activePultZone: [false, false, false, false, false, false, false, false],

            // обрыв линии
            lineToSpeakerErr: [false, false, false, false, false, false, false, false], // к пожару
            lineToFireErr: [false, false, false, false, false, false, false, false],    // к динамику
            connectToPult: true, // связь с пультом
            connectToBoxAKB: true, // связь с боксом АКБ
            connectToPower: true, // связь с 220В

            blocking: false, // блокирвка с пульта META18580
            fire: false, // пожар
            goI4S: false, // ГО и ЧС
            problem: false, // неисправность
            power: false, // питание
            offZummer: false, // откл зумерра
            offVolume: false, // откл звука
            signal: false, // сигнал
        };

        // перебираем все элементы и получаем новое состояние прибора
        elements.forEach(it => {
            switch (it.type) {
            case `lines`: // check lines breakPoint
                it.lines.forEach(item => {
                    // проверка алгоритма 3 = сигнал пожара только от 1
                    if (scheme.settings.algoritm === `3` && item.name === `toLineFire`) {
                        // линии к пожар 1
                        meta17820.lineToFireErr[item.fireID] = item.breakPoint.active;

                        if (item.fireID === 0 && item.breakPoint.active) {
                            meta17820.problem = true;
                        }

                    } else if (item.breakPoint && item.breakPoint.active) {
                        meta17820.problem = true;
                    }

                    if (item.breakPoint) {
                        if (item.name === `toMeta18580`) {
                            meta17820.connectToPult = !item.breakPoint.active;
                        }
                        else if (item.name === `toMeta17901`) {
                            meta17820.connectToBoxAKB = !item.breakPoint.active;
                        }
                        else if (item.name === `toPowerLine`) {
                            meta17820.connectToPower = !item.breakPoint.active;
                        }
                        else if (item.name === `toSpeaker`) {
                            meta17820.lineToSpeakerErr[item.speakerID] = item.breakPoint.active;
                        }
                    }
                })
                break
            case `signal`: // checkFire
                if (it.text[0] === `П` && it.active) { // сигнал пожара
                    meta17820.fireZone[it.zoneID] = true;
                    meta17820.fire = true;
                }
                else if (it.text[0] === `Г` && it.active) { // сигнал ГО и ЧС
                    meta17820.goI4S = true;
                }
                break
            case `meta18580`: // check
                if (it.keyOn) { // вкл блокировка на пульте
                    meta17820.blocking = true;
                }
                break
            }
        });

        // check power system  = connect to power
        meta17820.power = meta17820.connectToBoxAKB || meta17820.connectToPower;

        // check pult button
        meta18580.zoneControls.forEach((it,i) => {
            // зона активна если активна кнопка зоны или общая кнопка
            meta17820.activePultZone[i] = it.on || meta18580.controls[1].on
        });

        const clearSetSounds2ForFire = () => {
            clearTimeout(this.lastTimerID);
            scheme.settings.setSounds2ForFire = false;
        };

        // если есть питание, соеденение с блоком,
        // если нажата кнопка микр или сирена
        // если на пульте выбранна зона
        const isActivePult = () => meta17820.connectToPult &&
            meta17820.power &&
            (meta18580.controls[0].on || meta18580.micControls[0].on) &&
            meta17820.activePultZone.includes(true);

        // check pult
        // блокировика пульта, есть питание и есть связь с блоком и нажата сирена или микрофон
        if (meta17820.blocking && isActivePult()) {
            message.soundZones.text = MESSAGE.blocking;
            meta18580.zoneControls.forEach((it,i) => {
                // зона активна если активна кнопка зоны или общая кнопка
                meta17820.activeZone[i] = it.on || meta18580.controls[1].on
            })

            // если есть активная зона и включена сирена
            if (meta18580.controls[0].on && meta17820.activeZone.includes(true)){ // включена сирена
                track = `sirena`;
            }
        }

        // check Fire в зависимости от выбора алгоритма
        else if (meta17820.fire) {
            switch (scheme.settings.algoritm) {
            case `1`:
                if (!scheme.settings.setSounds2ForFire) { // вывод первого сообщения в 1 зоны
                    meta17820.activeZone = [true, false, false, false, false, false, false, false];
                    track = `attention`;
                    message.soundZones.text = MESSAGE.fire1Sound1Signal;
                    loopNewSound = false;

                    // через 30 сек запуск лругого сообщения
                    clearTimeout(this.lastTimerID); // сначала удалить старый таймаут
                    this.lastTimerID = setTimeout(() => {
                        this.setSounds2ForFire();
                    }, 10000) // TODO изменить на значение 30 сек
                } else { // вывод второго сообщения во все зоны
                    meta17820.activeZone = meta17820.fireZone;
                    meta17820.activeZone[0] = true
                    track = `fire`;
                    message.soundZones.text = MESSAGE.fire1Sound2Signal;
                }

                break
            case `2`:
                // вывод звука только в зоны пожара
                meta17820.activeZone = meta17820.fireZone;
                track = `fire`;
                message.soundZones.text = MESSAGE.fire2SoundSignal;
                clearSetSounds2ForFire(); // очистка от 2 сообщения пожара
                break
            case `3`:
                clearSetSounds2ForFire(); // очистка от 2 сообщения пожара
                // обрабатывается только первая линия пожара
                // вывод звука во все зоны
                if (meta17820.fireZone[0] === true) {
                    meta17820.activeZone = [true, true, true, true, true, true, true, true]
                    track = `fire`;
                    message.soundZones.text = MESSAGE.fire3SoundSignal;
                } else {
                    meta17820.activeZone = [false, false, false, false, false, false, false, false];
                    meta17820.fire = false;
                }
                break
            }
        }

        // check сигнал ГО и ЧС проходит, если нет пожара и неисправности
        else if (meta17820.goI4S) {
            clearSetSounds2ForFire(); // очистка от 2 сообщения пожара
            message.soundZones.text = MESSAGE.goI4S;
            meta17820.activeZone = [true, true, true, true, true, true, true, true];
            track = `go`;
            loopNewSound = true;
        }

        // нет никаких активных режимов: пожар, неисправность, ГО
        else {
            clearSetSounds2ForFire(); // очистка от 2 сообщения пожара
            // проверка пульта в режиме "откл" приоритета
            if (meta17820.blocking === false && isActivePult()) {
                message.soundZones.text = MESSAGE.blocking;

                meta18580.zoneControls.forEach((it,i) => {
                    // зона активна если активна кнопка зоны или общая кнопка
                    meta17820.activeZone[i] = it.on || meta18580.controls[1].on
                })

                // если есть активная зона и включена сирена
                if (meta18580.controls[0].on && meta17820.activeZone.includes(true)){ // включена сирена
                    track = `sirena`;
                }
            } else {
                scheme.message.soundZones.text = ``;
            }
        }

        // проверка зон на ошибки на линии
        meta17820.activeZone = this.getActiveZone(meta17820.lineToSpeakerErr, meta17820.activeZone)

        // установка нового звука, если звук совпадает со старым то продолжит играть старый трек
        if (meta17820.activeZone.includes(true)) {
            this.props.setTrack(track, loopNewSound);
        } else {
            this.props.setTrack(``, false);
        }

        // check ActiveZone
        // показать активные зоны (speaker)
        elements.map(it => {
            switch (it.type) {
            case `speaker`:
                it.soundActive = meta17820.activeZone[it.zoneID];
                break
            }

            return it
        });

        const signalStartSystem = meta17820.activeZone.includes(true);
        if (!signalStartSystem) { // если нет сигнала убраь сообщение
            scheme.message.soundZones.text = ``;
        }

        const offLEDPult = () => {
            meta18580.LEDs.forEach(it => it.on = false);
            meta18580.micControls[0].on = false;
            meta18580.controls.forEach(it => it.on = false);
            meta18580.zoneControls.forEach(it => it.on = false);
        }

        // check pult meta 18580
        if (!meta17820.connectToPult || !meta17820.power) { // если нет соединение или нет питания => пульт не работает
            offLEDPult();
        } else {
            meta18580.LEDs[0].on = meta17820.problem;
            meta18580.LEDs[1].on = true;
        }

        // check power system
        elements[12].active = meta17820.connectToPower; // line 220V
        elements[13].active = meta17820.connectToBoxAKB && !meta17820.connectToPower; // line АКБ

        // new view
        // 22 - meta17820
        // 23 - meta18580
        if (meta17820.power) {
            // диода на блоке 17820 = если нет питание не светятся
            elements[22].LEDs[0].on = meta17820.fire;
            elements[22].LEDs[1].on = meta17820.problem;
            elements[22].LEDs[2].on = true;
            elements[22].LEDs[5].on = signalStartSystem;

            // signal
            elements[10].active = meta17820.problem; // signal  `Неисправность`
            elements[11].active = signalStartSystem; // signal  `Сигнал запуска системы`
        } else {
            // offLEDBlock
            elements[22].LEDs.forEach(it => it.on = false);

            // off signal
            elements[10].active = false; // signal  `Неисправность`
            elements[11].active = false; // signal  `Сигнал запуска системы`
        }


        scheme.elements = elements;
        scheme.meta17820 = meta17820;

        this.setState({scheme});
    }

    // обработка изменения из схемы
    onEditorChange = (id, value) => {
        // id = index in scheme.elements
        const scheme = {...this.state.scheme};
        if (value) scheme.elements[id] = value;

        // проверка всей схемы с новыми условиями
        this.getCheckScheme(scheme)
    };
    render() {
        const {scheme} = this.state;
        return (
            <Editor
                scheme={scheme}
                onEditorChange={this.onEditorChange}
            />
        );
    }
}

const mapStateToProps = state => {
    return {
        register: state.steps.register,
    };
};
const mapDispatchToProps = (dispatch) => {
    return {
        setTrack: (track, loop) => dispatch(setTrack(track, loop)),
    }
}

SchemeMeta17820.propTypes = {
    settings: PropTypes.object,
    register: PropTypes.bool,
    setTrack: PropTypes.func,
};

export default connect(mapStateToProps, mapDispatchToProps)(SchemeMeta17820);
