import React from "react"
import {connect} from "react-redux"
import PropTypes from "prop-types"

import Typography from "@material-ui/core/Typography"
import Editor2 from "components/Editor2/Editor2.js"
import LayoutPage from "layout/LayoutPage/LayoutPage.js"

import META_17820 from "utils/const/scheme/meta17820.js"

import classes from "./Editor2Page.module.scss"

class Editor2Page extends React.Component {
    constructor(props){
        super(props)

        this.state = {
            settings: {...META_17820.settings},
        }
    }

    onSettingsChange = (field, value) => {
        const settings = {...this.state.settings};
        settings[field] = value;

        this.setState({settings});
    };

    render() {
        return (
            <LayoutPage>
                <Typography variant="h1" gutterBottom>
                    Редактор
                </Typography>
                <Typography variant="body1" gutterBottom>
                    версия 2.0
                </Typography>

                <div className={classes.settings}>

                </div>

                <Editor2 />
            </LayoutPage>
        );
    }
}

const mapStateToProps = state => {
    return {
        register: state.steps.register,
    };
};

Editor2Page.propTypes = {
    register: PropTypes.bool,
};

export default connect(mapStateToProps)(Editor2Page);
